import React from 'react';
import { Box, IconButton, Tooltip } from '@mui/material';
import { GitHub, LinkedIn, Email } from '@mui/icons-material';

const SocialLinks: React.FC = () => {
  return (
    <Box
      sx={{
        position: 'fixed',
        top: '50%',
        left: 0,
        transform: 'translateY(-50%)',
        // Hide on extra-small (xs) to small screens, show on medium (md) and above
        display: { xs: 'none', md: 'flex' },
        flexDirection: 'column',
        alignItems: 'center',
        gap: 1,
        padding: '0.5rem',
        backgroundColor: '#1E1E1E',
        borderRadius: '0 8px 8px 0',
        zIndex: 1000,
        boxShadow: '0 2px 8px rgba(0, 0, 0, 0.3)',
      }}
    >
      <Tooltip title="GitHub" placement="right">
        <IconButton
          href="https://github.com/MakiseKurisuX"
          target="_blank"
          sx={{
            color: 'white',
            transition: 'transform 0.2s ease-in-out',
            '&:hover': {
              color: '#FFD700',
              transform: 'scale(1.1)',
            },
          }}
        >
          <GitHub sx={{ fontSize: '2rem' }} />
        </IconButton>
      </Tooltip>

      <Tooltip title="LinkedIn" placement="right">
        <IconButton
          href="https://www.linkedin.com/in/ervintanzh"
          target="_blank"
          sx={{
            color: 'white',
            transition: 'transform 0.2s ease-in-out',
            '&:hover': {
              color: '#0077B5',
              transform: 'scale(1.1)',
            },
          }}
        >
          <LinkedIn sx={{ fontSize: '2rem' }} />
        </IconButton>
      </Tooltip>

      <Tooltip title="Email" placement="right">
        <IconButton
          href="mailto:ervintan018@gmail.com"
          target="_blank"
          sx={{
            color: 'white',
            transition: 'transform 0.2s ease-in-out',
            '&:hover': {
              color: '#FFD700',
              transform: 'scale(1.1)',
            },
          }}
        >
          <Email sx={{ fontSize: '2rem' }} />
        </IconButton>
      </Tooltip>
    </Box>
  );
};

export default SocialLinks;
